}

.services {
   /* background-image: url('/images/img-2.jpg'); */
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
   color: #fff;
   font-size: 100px;
}

.products {
   /* background-image: url('/images/img-1.jpg'); */
   background-position: center;
   background-size: fill;
   background-repeat: no-repeat;
   color: #fff;
   font-size: 100px;
}

.sign-up {
   /* background-image: url('/images/img-8.jpg'); */
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
   color: #fff;
   font-size: 100px;
}

.createJob_page{
   background-image: linear-gradient(135deg, #81ffef 10%, #f067b4 100%);
}

.createJob_jobInfo{
    display: flex;
    justify-content: space-evenly;
    border-radius: 30px;
    flex-direction: column;
    /* box-shadow: 20px 20px 50px 10px rgb(234, 0, 255); */
    padding: 2rem;
    background: rgb(226, 226, 226);
}

.createJob_mainForm{
    margin: 2rem;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    align-content: flex-start;
    padding: 2rem ;

}
.createJob_credit-section{
    background: rgb(226, 226, 226);
    border-radius: 30px;

    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.createJob_submitSection{
    color: white;
    display: flex;
    justify-content: center;
    padding: 1rem;
}

.createJob_imageSection{
    min-height: 500px;
    max-height: 500px;

    border-radius: 30px;
    padding: 1rem;
