    overflow-y: scroll
}


.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
 }
 
 .btn-hover {
    background-image: linear-gradient(
       to right,
       #9733ee,
       #da22ff,
       #da22ff,
       #9733ee
    );
    box-shadow: 0 3px 10px 0 #190030;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 55px;
    text-align: center;
    border: none;
    background-size: 300% 100%;
 
    border-radius: 50px;
    transition: all 0.4s ease-in-out;
 }
 
 .btn-hover:hover {
    background-position: 100% 0;
    transition: all 0.4s ease-in-out;
 }
 
 .btn-hover:focus {
    outline: none;
 }
 
 .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    box-shadow: 0 3px 10px 0 #21003d;
    border: 2px solid var(--primary);
    border-radius: 50px;
    transition: all 0.3s ease-out;
 }
 
 .btn--small {
    padding: 6px 15px;
    font-size: 16px;
    width: 110px;
    height: 45px;
 }
 
 .btn--medium {
    padding: 8px 20px;
    font-size: 18px;
 }
 
 .btn--large {
    padding: 12px 26px;
    font-size: 20px;
 }
 
 .btn--outline:hover {
    transition: all 0.3s ease-out;
    background: #da22ff;
    color: #ffffff;
    transition: 250ms;
 }
 
.footer-container {
   background-color: #242424;
   padding: 4rem 0 2rem 0;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.footer-subscription {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   text-align: center;

   margin-bottom: 24px;
   padding: 24px;
   color: #fff;
}

.footer-subscription > p {
   font-family: 'Montserrat', sans-serif;
}

.footer-subscription-heading {
   margin-bottom: 24px;
   font-size: 24px;
}

.footer-subscription-text {
   margin-bottom: 24px;
   font-size: 20px;
}

.footer-input {
   padding: 8px 20px;
   border-radius: 2px;
   margin-right: 10px;
   outline: none;
   border: none;
   font-size: 18px;
   margin-bottom: 16px;
   border: 1px solid #fff;
}

.footer-links {
   width: 100%;
   max-width: 1000px;
   display: flex;
   justify-content: center;
}

.footer-link-wrapper {
   display: flex;
}

.footer-link-items {
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   margin: 16px;
   text-align: left;
   width: 160px;
   box-sizing: border-box;
}

.footer-link-items h2 {
   margin-bottom: 16px;
}

.footer-link-items > h2 {
   color: #fff;
}

.footer-link-items a {
   color: #fff;
   text-decoration: none;
   margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
   color: #e9e9e9;
   transition: 0.3s ease-out;
}

.footer-email-form h2 {
   margin-bottom: 2rem;
}

.footer-input::-webkit-input-placeholder {
   color: #b1b1b1;
}

.footer-input:-ms-input-placeholder {
   color: #b1b1b1;
}

.footer-input::placeholder {
   color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
   color: #fff;
   font-size: 24px;
}

.social-media {
   max-width: 1000px;
   width: 100%;
}

.social-media-wrap {
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 90%;
   max-width: 1000px;
   margin: 40px auto 0 auto;
}

.social-icons {
   display: flex;
   justify-content: space-between;
