}

.social-logo {
   color: #fff;
   justify-self: start;
   cursor: pointer;
   text-decoration: none;
   font-size: 2rem;
   display: flex;
   align-items: center;
}

.website-rights {
   color: #fff;
   margin-bottom: 16px;
   font-family: 'Montserrat', sans-serif;
}

@media screen and (max-width: 820px) {
   .footer-links {
